img {
  max-width: 100%;
  height: auto;
}

@font-face {
  font-family: sans-serif;
}
.main-header {
  font-size: 30px;
  font-weight: bold;
}

.page {
  font-family: "Arial", sans-serif;
  height: 100%;
  font-size: calc((1vw + 1vh) * 0.9);
  line-height: 1.8;
}

.page__body {
  background-color: #0c0500;
  color: #ffffff;
  min-width: 320px;
  min-height: 100%;
  height: 2900px;
  margin: 0 auto;
}

.main-content {
  transform-style: preserve-3d;
  height: 100%;
}

.container {
  width: 100%;
  height: 100%;
  position: fixed;
  perspective: 1500px;
}

.gallery {
  transform-style: preserve-3d;
  height: 100%;
}

.frame {
  width: 100%;
  height: 100%;
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 0.6s cubic-bezier(0.16, 1, 0.3, 1), opacity 0.7s ease;
  will-change: transform;
  transform-style: preserve-3d;
}
.frame--bg {
  background-color: rgba(12, 5, 0, 0.9);
}
.frame--bg-blur {
  backdrop-filter: blur(4px);
}

.frame__title {
  font-weight: 100;
  text-transform: uppercase;
  text-align: center;
  font-size: calc((1vw + 1vh) * 3);
  line-height: 1;
  letter-spacing: 5px;
  margin-top: 0;
  margin-bottom: 0;
}

.frame__image {
  display: block;
  position: relative;
  background-color: #d38440;
  object-fit: cover;
  width: calc((1vw + 1vh) * 36);
  height: calc((1vw + 1vh) * 26);
}
.frame__image--left {
  right: calc(18 * (1vw + 1vh) + 30px);
}
.frame__image--right {
  left: calc(18 * (1vw + 1vh) + 30px);
}
.frame__image--vertical {
  width: calc((1vw + 1vh) * 26);
  height: calc((1vw + 1vh) * 36);
  right: calc(13 * (1vw + 1vh) + 30px);
}
.frame__image--vertical-left {
  left: calc(13 * (1vw + 1vh) + 30px);
}

.frame__content--text-right {
  position: relative;
  left: 18vw;
}
@media (max-width: 767px) {
  .frame__content--text-right {
    left: 25vw;
  }
}
.frame__content--text-left {
  position: relative;
  right: 18vw;
  text-align: right;
}
@media (max-width: 767px) {
  .frame__content--text-left {
    right: 25vw;
  }
}
.frame__content .frame__subtitle {
  font-weight: 100;
  text-transform: uppercase;
  font-size: calc((1vw + 1vh) * 1.5);
  line-height: 1;
  letter-spacing: 1px;
  margin-top: 0;
  margin-bottom: 0;
}
.frame__content .frame__text {
  max-width: 30vw;
  margin-top: 3vh;
}
@media (max-width: 767px) {
  .frame__content .frame__text {
    max-width: 40vw;
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.form__container {
  opacity: 0;
  position: absolute;
  background-color: #0c0500;
  background-image: url("../img/myself-33.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  width: 100%;
  height: 1px;
  top: 45%;
  transition: top 1s ease, height 1s ease, opacity 1.7s ease;
  z-index: 1;
}
.form__container--show {
  height: 100%;
  opacity: 1;
  top: 0;
}
.form__container--show .form__content {
  transition: height 1s ease, opacity 1s ease;
  height: 100%;
  overflow: visible;
  opacity: 1;
}

.form__container::before {
  content: "";
  position: absolute;
  inset: 0;
  z-index: -1;
  background-color: rgba(0, 0, 0, 0.7);
}

.form__content {
  position: absolute;
  left: 50%;
  transition: opacity 1s ease 0s, height 1s ease 0s;
  opacity: 0;
  height: 0;
  overflow: hidden;
  padding-top: 40px;
  max-width: 600px;
}
@media (min-width: 768px) {
  .form__content {
    padding-right: 10vw;
  }
}
@media (max-width: 767px) {
  .form__content {
    transform: translateX(-50%);
    padding-right: 10px;
    padding-left: 10px;
  }
}
.form__content .form__title {
  font-weight: 300;
  text-transform: uppercase;
  font-size: calc((1vw + 1vh) * 1.1);
  line-height: 1.3;
  margin-top: 0;
  margin-bottom: 10px;
  letter-spacing: 1px;
}
.form__content .form__text {
  margin-top: 0;
  margin-bottom: 0;
  line-height: 1.2;
  letter-spacing: 1px;
}
@media (max-width: 767px) {
  .form__content .form__text {
    margin-bottom: 15px;
  }
}

.feedback {
  position: relative;
}
.feedback::after {
  content: "";
  position: absolute;
  inset: 0;
  background-image: url("../img/loading.png");
  background-repeat: no-repeat;
  background-position: center;
  background-color: rgba(12, 5, 0, 0.4);
  box-shadow: 0 0 15px #0c0500;
  opacity: 0;
  visibility: hidden;
  transition: all 0.5s ease;
}
.feedback--sending::after {
  opacity: 1;
  visibility: visible;
}

.feedback__list {
  padding: 0;
  list-style: none;
}
@media (min-width: 768px) {
  .feedback__list {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 20px;
  }
  .feedback__list .feedback__item:nth-child(3),
.feedback__list .feedback__item:nth-child(4) {
    grid-column: span 2;
  }
}

.feedback__btn {
  position: relative;
  background-color: transparent;
  border: 2px solid #ffce96;
  padding: 10px 20px;
  width: 100%;
  max-width: 180px;
  color: #ffce96;
  letter-spacing: 0.5px;
  text-transform: uppercase;
  display: block;
  margin-left: auto;
  cursor: pointer;
  outline: none;
  font-size: 1rem;
  transition: color 0.5s ease, background-color 1.7s ease;
}
@media (max-width: 767px) {
  .feedback__btn {
    padding: 6px 10px;
  }
}
.feedback__btn::before {
  content: "";
  position: absolute;
  background-color: #ffce96;
  z-index: -1;
  height: 100%;
  width: 0;
  left: 50%;
  top: 0;
  transform: translate(-50%);
  transition: width 0.6s ease;
}
.feedback__btn:hover::before {
  width: 100%;
}
.feedback__btn:hover {
  background-color: #ffce96;
  color: #0c0500;
}

.field-item {
  position: relative;
}
@media (max-width: 767px) {
  .field-item {
    margin-bottom: 15px;
  }
}

.field-item__input {
  width: 100%;
  min-height: 35px;
  font-size: 1rem;
  line-height: 1.1;
  border: 2px solid rgba(58, 159, 201, 0.3);
  transition: all 0.5s ease 0s;
  outline: none;
  background-color: transparent;
  box-sizing: border-box;
  padding-left: 5px;
  padding-top: 5px;
  padding-bottom: 3px;
  color: rgba(58, 159, 201, 0.7);
}
@media (max-width: 767px) {
  .field-item__input {
    min-height: 20px;
    padding-left: 3px;
  }
}
.field-item__input--area {
  resize: none;
  min-height: 140px;
}
@media (max-width: 767px) {
  .field-item__input--area {
    min-height: 70px;
  }
}
.field-item__input:hover, .field-item__input:focus {
  border-color: #3a9fc9;
}
.field-item__input:hover ~ .field-item__name, .field-item__input:focus ~ .field-item__name, .field-item__input:not(:placeholder-shown) ~ .field-item__name {
  font-size: calc((1vw + 1vh) * 0.4);
  transform: translate(12px, -9px);
  background-color: #3a9fc9;
  color: #0c0500;
  padding: 3px 5px;
}
@media (max-width: 767px) {
  .field-item__input:hover ~ .field-item__name, .field-item__input:focus ~ .field-item__name, .field-item__input:not(:placeholder-shown) ~ .field-item__name {
    transform: translate(8px, -5px);
  }
}
.field-item__input--error {
  border-color: #ff4040;
  box-shadow: 0 0 10px #ff4040;
}

.field-item__name {
  font-size: calc((1vw + 1vh) * 0.7);
  text-transform: uppercase;
  color: rgba(58, 159, 201, 0.4);
  position: absolute;
  pointer-events: none;
  left: 0;
  padding: 8px;
  letter-spacing: 0.5px;
  font-weight: 300;
  line-height: 1;
  transition: all 0.5s ease;
}
@media (max-width: 767px) {
  .field-item__name {
    padding: 5px;
  }
}